




































































































































































































































































































































































import { Component, Vue } from 'vue-property-decorator'
import { ElForm } from 'element-ui/types/form'
import * as m from '@/api/model'
import * as api from '@/api/api'

@Component({})
export default class CourseClass extends Vue {
  data:m.CourseClass[] = []
  total = 0
  loading = true
  courseClass: m.CourseClass = {} as any
  semesterList:m.Semester[] = []
  teacherList:m.Teacher[] = []
  courseList:m.Course[] = []
  showDialog = false
  editForm:m.CreateCourseClassForm={} as any
  showCheckbox = false
  selectCourseClassId:number[] = []

  showExcelDialog=false

  statusOption = [{
    value: 0,
    label: '正常'
  }, {
    value: 1,
    label: '异常'
  }, {
    value: 2,
    label: '未知'
  }]

  queryOptions = {
    semester: '',
    teacher: '',
    course: '',
    page: 1,
    pageSize: 20
  }

  rules={
    no: [{ required: true, message: '课程序号不能为空', trigger: 'blur' }],
    semesterId: [{ required: true, message: '学期不能为空', trigger: 'blur' }],
    teacherId: [{ required: true, message: '执教老师不能为空', trigger: 'blur' }],
    courseId: [{ required: true, message: '课程名称不能为空', trigger: 'blur' }]
  }

  created() {
    this.init()
  }

  async init() {
    this.requestData()
    this.querySemesterList('')
    this.queryTeacherList('')
    this.queryCourseList('')
  }

  handleFilter() {
    this.queryOptions.page = 1
    this.requestData()
  }

  async requestData() {
    this.loading = true
    const res = await this.api.queryCourseClass(this.queryOptions)
    this.data = res.data.list
    this.total = res.data.total
    this.loading = false
  }

  handleCreate() {
    this.courseClass = {} as any
    this.resetForm()
    this.showDialog = true
  }

  resetForm() {
    this.editForm = {
      no: '',
      semesterId: undefined,
      teacherId: undefined,
      courseId: undefined,
      startWeek: 1.0,
      endWeek: 1.0,
      status: undefined
    }
  }

  handleEdit(courseClass: m.CourseClass) {
    this.courseClass = courseClass
    this.editForm = {
      no: courseClass.no,
      semesterId: courseClass.semesterId,
      teacherId: courseClass.teacherId,
      courseId: courseClass.courseId,
      startWeek: courseClass.startWeek,
      endWeek: courseClass.endWeek,
      status: courseClass.status
    }
    this.showDialog = true
  }

  handleSave() {
    (this.$refs['editForm'] as ElForm).validate(async(valid : boolean) => {
      if (valid) {
        if (this.courseClass.id) {
          const res = await this.api.putCourseClass(this.courseClass.id, this.editForm)
          if (res.status === 0) {
            this.resetForm()
            this.showDialog = false
            this.$message({
              type: 'success',
              message: '修改成功!'
            })
            this.requestData()
          }
        } else {
          const res = await this.api.createCourseClass(this.editForm)
          if (res.status === 0) {
            (this.$refs['editForm'] as ElForm).resetFields()
            this.showDialog = false
            this.$message({
              type: 'success',
              message: '添加成功!'
            })
            this.requestData()
          }
        }
      } else {
        return false
      }
    })
  }

  handleDelete(courseClass: m.CourseClass) {
    this.$confirm(`确定删除${courseClass.no}吗？`, '提示', {
      type: 'warning'
    }).then(async() => {
      const resp = await this.api.deleteCourseClass(courseClass.id)
      if (resp.status === 0) {
        this.$message({
          type: 'success',
          message: '删除成功!'
        })
        this.data = this.data.filter((e: m.CourseClass) => e.id !== courseClass.id)
        if (this.total > 1) {
          this.total--
        } else {
          this.requestData()
        }
      }
    })
  }
  async querySemesterList(query: string) {
    const option = {
      page: 1,
      pageSize: 20,
      name: query
    }
    const res = await api.querySemester(option)
    if (res.status === 0 && res.data.list.length > 0) {
      this.semesterList = res.data.list
    }
  }

  async queryTeacherList(query: string) {
    const option = {
      page: 1,
      pageSize: 20,
      name: query
    }
    const res = await api.queryTeacher(option)
    if (res.status === 0 && res.data.list.length > 0) {
      this.teacherList = res.data.list
    }
  }

  async queryCourseList(query: string) {
    const option = {
      page: 1,
      pageSize: 20,
      name: query
    }
    const res = await api.queryCourse(option)
    if (res.status === 0 && res.data.list.length > 0) {
      this.courseList = res.data.list
    }
  }
  async queryCourseClassList(query: string, callback: any) {
    const option = {
      page: 1,
      pageSize: 20,
      no: query
    }
    const res = await api.queryCourseClass(option)
    let list = [{}]
    if (res.status === 0 && res.data.list.length > 0) {
      list = res.data.list
      list.forEach((element:any) => {
        element.value = element.no
      })
    }
    callback(list)
  }

  handleSelect(select:m.CourseClass[]) {
    this.selectCourseClassId = select.map((item:m.CourseClass) => {
      return item.id
    })
  }

  handleBatchDelete() {
    if (this.selectCourseClassId.length < 1) {
      this.$message({
        type: 'warning',
        message: '请先选择要删除项'
      })
      return
    }
    this.$confirm(`确定要批量删除所选项吗？`, '提示', {
      type: 'warning'
    }).then(async() => {
      const resp = await this.api.batchDeleteCourseClass(this.selectCourseClassId)
      if (resp.status === 0) {
        this.$message({
          type: 'success',
          message: '删除成功!'
        })
        this.requestData()
      }
    })
  }
}
